import { useEffect } from "react";
import $ from "jquery";

function ScrollBar() {
    function scrollBar(sts) {
        var _windowTop = $(window).scrollTop();
        var _windowHeight = $(window).height();
        var _pageHeight = $("body").height() - $(window).height();
        _windowTop = $(window).scrollTop();
        _pageHeight = $("body").height() - _windowHeight;
        var _ratio = (_windowTop / _pageHeight) * 100;
        $("#scroll_bar .current_indicator").css("top", _ratio + "%");
        // console.log(sts);
    }

    useEffect(() => {
        $(window).on("scroll", function () {
            //common
            scrollBar("scroll bar - scrolling");
        });
        scrollBar("scroll bar - start"); //
    });
    return (
        <aside id="nav_scroll">
            <div id="scroll_bar">
                <div className="inner">
                    <span className="current_indicator"></span>
                </div>
            </div>
            <a
                data-use="scroll"
                title="  "
                id="scroll_top"
                href="#body-layout"
            >
                {" "}
            </a>
        </aside>
    );
}

export default ScrollBar;
